.card {
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
	background-color: var(--background-secondary);
	border-radius: var(--radius-lg);
	padding: 2.5rem;
	transition: background var(--transition-duration) var(--transition-type);

	&__header,
	&__profile {
		width: 100%;
		display: flex;
		gap: 1rem;
	}

	&__header {
		align-items: flex-start;
		justify-content: space-between;
	}

	&__profile {
		align-items: center;

		&-avatar {
			width: 8.875rem;
			height: 8.875rem;
			display: block;
			flex-shrink: 0;
	
			.image {
				display: block;
				width: 100%;
				height: 100%;
				border-radius: 50%;
			}
		}

		&-name {
			display: flex;
			align-items: center;
			gap: .25rem 1rem;
			margin-bottom: 1rem;

			.btn {
				--_color: inherit;
			}
		}

		&-location {
			display: flex;
			align-items: center;
			gap: .5rem;
			font-size: var(--fs-lg);
			font-weight: 600;
			margin-bottom: .875rem;
		}

		&-link {
			--_border-color-hover: var(--text-color);
			--_border-radius: var(--radius-md);
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}

	&__reviews,
	&__statistics {
		white-space: nowrap;
		margin-bottom: 1rem;
	}

	&__reviews {
		--_font-size: var(--fs-lg);
		--_font-weight: 700;
		
		gap: .875rem;
	}

	&__statistics {
		--_font-weight: 700;
		--_color: var(--dark-color);

		gap: 1rem;
	}

	&__swiper {
		width: auto;
		margin-inline: -2.5rem;

		.swiper {
			padding-inline: 2.5rem;

			&-slide {
				width: auto;

				&-picture {
					width: 21.75rem;
					height: 14rem;
					display: block;
	
					.image {
						display: block;
						width: 100%;
						height: 100%;
						border-radius: var(--radius-lg);
					}
				}
			}
		}

		.swiper-scrollbar {
			margin: 1.5rem 2.5rem 0;
		}
	}

	&__language {
		width: 16.25rem;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-end;
		gap: 1rem;
		font-size: var(--fs-lg);
		font-weight: 700;
	}

	&__price {
		display: flex;
		align-items: flex-start;
		gap: .875rem;
		font-size: var(--fs-lg);
		color: var(--dark-color);
		background-color: var(--background-inverse);
		border-radius: var(--radius);
		padding: 1rem 2.5rem;

		&-info {
			display: flex;
			flex-direction: column;
			gap: .75rem;
		}

		&-title {
			font-size: inherit;
			font-weight: 400;
			margin-bottom: 0;
		}

		&-value {
			font-weight: 700;
		}
	}

	&--light {
		color: var(--dark-color);
		background-color: var(--background-lighter);
	}

	&--header {
		.card__language {
			width: 12.5rem;
		}
	}

	&--review {
		font-size: var(--fs-lg);
		
		.card__body {
			display: flex;
			align-items: flex-start;
			gap: 1rem;

			&-start {
				flex-shrink: 0;

				&_image {
					width: 8.75rem;
					height: 8.75rem;
					display: block;
				}

				.card__body-title {
					display: none;
				}
			}

			&-end {

				> .card__time {
					display: none;
					margin-top: 1.5rem;
				}
			}

			&-header {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;
				gap: .25rem 1rem;
				margin-bottom: 1.5rem;
			}

			&-title {
				margin-bottom: 0;
			}

			&-main {

				&_text {
					line-height: 1.9;
				}
			}
		}
	}

	&--edit {

		.card__profile {
			align-items: flex-start;
		}

		.card__reviews {
			--_color: var(--dark-color);
		}
	}
}